@-webkit-keyframes progress-bar {
   0% { width: 0%; }
}

@keyframes progress-bar {
   0% { width: 0%; }
}

@-webkit-keyframes niveau {
   0%   { opacity: 0; },
   100% {opacity: 1; }
}

@keyframes niveau {
   0%   { opacity: 0; },
   100% {opacity: 1; }
}

.blue{
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(3, 31, 70, 1)),
    color-stop(15%, rgba(3, 31, 70, 1)),
    to(rgba(11, 113, 255, 1))
    );
  background: -webkit-linear-gradient(
    left,
    rgba(3, 31, 70, 1),
    rgba(3, 31, 70, 1) 15%,
    rgba(11, 113, 255, 1) 100%
    );
  background: -o-linear-gradient(
    left,
    rgba(3, 31, 70, 1),
    rgba(3, 31, 70, 1) 15%,
    rgba(11, 113, 255, 1) 100%
    );
  background: linear-gradient(
    to right,
    rgba(3, 31, 70, 1),
    rgba(3, 31, 70, 1) 15%,
    rgba(11, 113, 255, 1) 100%
    );
  border-radius: 20px;
  -webkit-animation: progress-bar 4s;
          animation: progress-bar 4s;
}

.progress {
  width: 300px;
  height: 10px;
  border: 1px solid;
  border-radius: 20px;
  grid-area: progres;
  position: relative;
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-top: 6px;
}

.lang {
  grid-area: lang;
  font-weight: 700;
}

.grid-lang {
  display: grid;
  grid-template-areas: "lang progres";
  grid-template-columns: 140px 300px;
  -ms-flex-align: center;
      align-items: center;
  margin: 0px 40px;
}

.langs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 98%;
  width: 1040px;
  position: relative;
  margin: 10px 0;
  padding: 15px 0;
   border-radius: 20px;
  border: solid 1px rgba(40, 40, 40, 0.30);
  -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.75);
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.75);
}

.competences {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.come {
  left: 22%;
  position: relative;
  margin: 0;
  padding-bottom: 40px;
}

#niveau{
  -ms-flex-item-align: end;
      align-self: end;
  margin-bottom: 10px;
  font-size: 13px;
}

.opacity-anim{
  -webkit-animation: niveau 4s ease-out;
          animation: niveau 4s ease-out;
}

@media screen and (max-width: 1420px) {
  .langs{
    left: 4%;
    width: 940px;
  }

  .progress {
    width: 250px;
    height: 10px;
  }

  .grid-lang {
    grid-template-columns: 140px 250px;
  }

}

@media screen and (max-width: 1100px) {

  .langs{
    width: 840px;
  }


  .progress {
    width: 200px;
    height: 10px;
  }

  .grid-lang {
    grid-template-columns: 140px 200px;
  }
}

@media screen and (max-width: 960px) {
  .langs{
    width: 740px;
    left: 0%;
  }

  .lang{
    font-size: 14px;
  }

  .grid-lang {
    grid-template-columns: 130px 200px;
    margin: 0 20px;
  }
}

@media screen and (max-width: 755px) {

  .langs {
    -ms-flex-pack: center;
        justify-content: center;
  }

  .come {
    left: 0%;
    text-align: center;
  }

}

@media screen and (max-width: 350px) {
  .lang {
    font-size: 13px;
  }

  .grid-lang {
    grid-template-columns: 115px 150px;
  }

  .progress {
    width: 150px;
    height: 10px;
  }
}



